.links{
    text-decoration:none;
    margin-left:10px;
    transition:all 0.2s ease;
    color:black;
}
.links:hover{
    text-decoration:none;
    color:white;
    opacity:0.8;
  
}
.links:active{
    color:white;
}

.blacknav{
    background:rgba(0,0,0, 0.07);
    font-size:30px;
    border-radius:10px;
    padding-right:10px;
    display:flex;
    
}

.App{
    background:url('../../img/kv-design-banner.png');
    background-size: 100% 100%;
    padding-bottom:30px;
